.sideDrawer {
  position: fixed;
  margin-top: -32px;
  width: 280px;
  max-width: 70%;
  height: 100%;
  left: 0px;
  z-index: 200;
  background-color: #1c1c1c;
  padding: 32px 16px;
  box-sizing: border-box;
  transition: transform 0.3 ease-out;
}
@media (min-width: 500px) {
  .sideDrawer {
    display: none;
  }
}
.sideDrawer img {
  height: 3em;
  margin-bottom: 30px;
}
.Open {
  transform: translateX(0);
}
.Close {
  transform: translateX(-100%);
}
.sideDrawerOpener {
  position: fixed;
  left: 0px;
  top: 36px;
  z-index: 150;
  background-color: #1c1c1c;
  outline: none;
  border: none;
}
.sideDrawerOpener img {
  height: 10vw;
}

@media (min-width: 500px) {
  .sideDrawerOpener {
    position: absolute;
    left: -1000px;
  }
}
