.NavItem {
  margin: auto 5px;
  color: white;
  display: flex;
  align-items: center;
  box-sizing: border-box;
}
.NavItem a {
  color: white;
  text-decoration: none;
  border-bottom: 4px solid #afafafff;
  box-sizing: border-box;
  display: block;
}
.NavItem a:hover,
.NavItem a:focus,
.NavItem a:active {
  border-bottom: 4px solid #afaf33ff;
}
@media (max-width: 499px) {
  .NavItem {
    margin-bottom: 2em;
  }
}
