.toolbar {
  background-color: #1c1c1c;
  position: fixed;
  left: 0;
  top: 0;
  height: 56px;
  width: 100%;
  display: flex;
  z-index: 90;
  justify-content: space-between;
  align-items: center;
  color: white;
}
.toolbar img {
  height: 80%;
}
@media (max-width: 499px) {
  .toolbar {
    display: none;
  }
}
